import { Component, OnInit } from '@angular/core';
import { InventariosService } from '../../providers/Api/Inventarios.service';
import { InicioService } from '../../providers/Almacenamiento/inicio.service';
import { AlertController } from '@ionic/angular';
import { ToastServices } from '../../providers/Utilidades/ToastService.service';

@Component({
  selector: 'app-empleados',
  templateUrl: './empleados.page.html',
  styleUrls: ['./empleados.page.scss'],
})
export class EmpleadosPage implements OnInit {

  empleados: any = [];
  buscados = [];

  texto = '';

  constructor(
    private IVTapi: InventariosService,
    private almacenamiento: InicioService,
    private alertCtrl: AlertController,
    private toast: ToastServices
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
      this.traerEmpleados();
  }

  refrescando( event) {
    this.traerEmpleados().then(() => {
      event.target.complete();
    }).catch(() => {
      event.target.complete();
    });
  }

  async traerEmpleados() {
    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
        this.IVTapi.leerEmpleados(info.token).subscribe((data: any) => {
            console.log(data);
            if (data.exe) {

                data.response.forEach((item) => {
                  if (item.usuario.imagen.length > 0) {
                    item.usuario.imagen = `https://motocaliapp.com/19927613410/${item.usuario.imagen[0].path}`;
                  } else {
                    item.usuario.imagen = '/assets/notfound.png';
                  }
                })
                this.empleados = data.response;
            }
        })
      }
  }

  async buscarEmpleados( event ) {
      const info = await this.almacenamiento.obtenerToken();

      if ( info && event.detail.value !== '') {
        this.IVTapi.BuscarUsuarios(info.token, event.detail.value).subscribe((data: any) => {
            console.log(data);
            if (data.exe) {

                data.response.forEach((item) => {
                  if (item.imagen.length > 0) {
                    item.imagen = `https://motocaliapp.com/19927613410/${item.imagen[0].path}`;
                  } else {
                    item.imagen = '/assets/notfound.png';
                  }
                })
                this.buscados = data.response;
            }
        })
      } else {
        this.buscados = [];
      }
  }

  async agregar( usuario: any ) {
    const alert = await this.alertCtrl.create({
        header: 'Marcar usuario como empleado',
        message: `¿Estas seguro de marcar a ${usuario.nombrecompleto} como empleado?`,
        buttons: [
          {
            role: 'cancel',
            text: 'Cancelar'
          },
          {
            text: 'Aceptar',
            handler: async () => {
              const info = await this.almacenamiento.obtenerToken();

              if ( info ) {
                this.IVTapi.crearEmpleado(info.token, {nombre: usuario.nombrecompleto, _id: usuario._id}).subscribe((data: any) => {
                    console.log(data);
                    if (data.exe) {
                        this.traerEmpleados();
                    } else {
                      if (data.err.errors) {
                        if (data.err.errors.usuario) {
                          if (data.err.errors.usuario.kind === 'unique') {
                              this.toast.toastError('El usuario ya ha sido agregado como empleado');
                          }
                        }
                      }
                    }
                })
              }
            }
          }
        ]
    })

    return await alert.present();
  }


  async eliminar( empleado: any ) {
    const alert = await this.alertCtrl.create({
        header: 'Eliminar Empleado',
        message: `¿Estas seguro de eliminar a ${empleado.usuario.nombrecompleto} como empleado?`,
        buttons: [
          {
            role: 'cancel',
            text: 'Cancelar'
          },
          {
            text: 'Aceptar',
            handler: async () => {
              const info = await this.almacenamiento.obtenerToken();

              if ( info ) {
                this.IVTapi.eliminarEmpleados(info.token, empleado._id).subscribe((data: any) => {
                    if (data.exe) {
                        this.traerEmpleados();
                    } else {
                      this.toast.toastError('No se pudo eliminar el empleado');
                    }
                }, (err) => {
                  this.toast.toastError('No se pudo eliminar el empleado, intentelo más tarde');
                })
              }
            }
          }
        ]
    })

    return await alert.present();
  }



  async editar( empleado: any ) {
    const alert = await this.alertCtrl.create({
        header: 'Eliminar Empleado',
        message: `¿Estas seguro de eliminar a ${empleado.usuario.nombrecompleto} como empleado?`,
        inputs: [
          {
            type: 'text',
            name: 'cargo',
            placeholder: `Escriba el cargo ${empleado.usuario.nombrecompleto}`
          },

          {
            type: 'text',
            name: 'alias',
            placeholder: `Escribe un alías`
          },

        ],
        buttons: [
          {
            role: 'cancel',
            text: 'Cancelar'
          },
          {
            text: 'Aceptar',
            handler: async (re) => {
              const info = await this.almacenamiento.obtenerToken();

              if ( info ) {
                this.IVTapi.editarEmpleado(info.token, {_id: empleado._id, cargo: re.cargo, alias: re.alias}).subscribe((data: any) => {
                    if (data.exe) {
                        this.traerEmpleados();
                    } else {
                      this.toast.toastError('No se pudo editar el empleado');
                    }
                }, (err) => {
                  this.toast.toastError('No se pudo editar el empleado, intentelo más tarde');
                })
              }
            }
          }
        ]
    })

    return await alert.present();
  }

}
